import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import AccordionDetails from '@mui/material/AccordionDetails';

const dispatchedEvents = [];

const SrsMuiAccordionDetails = function (props, ref) {
  let componentProps = {...props, theme: props.muiTheme};
  return (
    <AccordionDetails {...componentProps}>
      <slot />
    </AccordionDetails>
  );
}

const styles = `
  /* Custom styles for the AccordionDetails component */
`;

const props = [];

const slots = [
  {
    name: "default",
    allowed: [],
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const WrappedComponent = wrapper(React.forwardRef(SrsMuiAccordionDetails), props, "mui");
export { WrappedComponent, styles, props, dispatchedEvents, slots };